import React from 'react';
import {Route, Switch, BrowserRouter} from 'react-router-dom'

import Container from 'react-bootstrap/Container'

import '../css/style.css';
import HomePage from './homePage';
import Salon from './salon';
import SignIn_recovery from './signIn_recovery';
import UserPage from './userPage';
import Footer from './footer';

function Home(props) {
	return (	
		<div id="home" className="full-height">
			<div className="full-height-content">
				<Container>				
						<BrowserRouter>					
							<Switch>			
								<Route path="/table">
									<UserPage></UserPage>
								</Route>
								<Route path="/salon">
									<Salon></Salon>
								</Route>
								<Route path="/recovery">
									<SignIn_recovery></SignIn_recovery>
								</Route>
								<Route path="/">
									<HomePage response={props}></HomePage>
								</Route>
							</Switch>			
						</BrowserRouter>
				</Container>
				<Footer></Footer>
			</div>			
		</div>
	);
}

export default Home;
