import React, { Component } from 'react';
import {Route, Switch, BrowserRouter} from 'react-router-dom'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import NavDropdown from 'react-bootstrap/NavDropdown'

import socketIOClient from "socket.io-client/dist/socket.io";
import $ from 'jquery'; 
import logo_icon from '../img/logo.png';
import carrot_img from '../img/icons/carrot_icon.png';

import Game from './game';
import UserAccount from './userAccount';

const socket = socketIOClient("/");

class UserPage extends Component {
	constructor(props) {
		super(props);
		this.handleClick = this.handleClick.bind(this);
	}
	
	state = {
		user: '',
		visible: false,
	}; 
	
	handleClick(link) {		
		if(link === "account"){			
			this.setState({ visible: true })
		} else if(link === "casino"){
			this.setState({ visible: false })
		} else {
			var url_back = window.location.href.split('/table/');
			window.location.href = url_back[0];
		}
	}
  
	componentDidMount() {
		this.callApi()
			.then(res => {				
					this.setState({ user: res.server_user });
					var table = this.state.user.user_table;
					var table_split = table.split('_');
					var table_user = table_split[0] + ' ' + table_split[1];
					var table_type = table_split[2];
					var payload = {user: this.state.user.user, user_table: table_user, user_type: table_type, time: new Date().getTime()}
					socket.emit('username', payload);
					socket.on('is_online', function(data) {
						$('#chatmessages').append(data);
					});
				})
			.catch(err => console.log(err));  
		
		
		$("#user_icon").click(function(){
			$('.user_details_container').toggleClass('open');
		});
		$(window).click(function(e) {			
			if(e.target.id !== "user_icon_path" && e.target.id !== "user_icon" && e.target.id !== "user_details"){
				$('.user_details_container').removeClass('open')
			} 
		});
	}
  
	callApi = async () => {
		var table = window.location.href.split('table/')
		const response = await fetch('/table/'+table[1]);
		const body = await response.json();
		if (response.status !== 200) throw Error(body.message);
		return body;
	};
  
  
	render() {		
		if(this.state.user.user === ""){
			var url_back = window.location.href.split('/table/');
			window.location.href = url_back[0];
		}
		
		var username = this.state.user.user;
		var url = '/table/'+this.state.user.user_table;
		var money = this.state.user.money;
		var type = "";
		var user_table = ""
		var game = this.state.user.game;
		
		if(typeof this.state.user.user_table != "undefined"){
			var user_table_text = this.state.user.user_table.split('_');
			type = user_table_text[2];
			user_table = user_table_text[0] + ' ' + user_table_text[1]
		}		
		
		if(typeof money === "undefined"){
			money = 0;
		}
		
		return (			
			<BrowserRouter>
				<div className="userPage"> 
					<Navbar bg="light" expand="lg">
						<div className="logo_title">
							<Navbar.Brand href="/">
								<img className="logo_title_img" src={logo_icon} />
								<div className="logo_title_text">BunnyBet</div>
							</Navbar.Brand>							
						</div>
						{/* <Navbar.Toggle aria-controls="basic-navbar-nav" />
						<Navbar.Collapse id="basic-navbar-nav">
							<Nav className="mr-auto">
								<Nav.Link href="/">Home</Nav.Link>								
								<NavDropdown title="Table games" id="basic-nav-dropdown">
									<NavDropdown.Item href="/">Action</NavDropdown.Item>
									<NavDropdown.Item href="/">Another action</NavDropdown.Item>
									<NavDropdown.Item href="/">Something</NavDropdown.Item>
								</NavDropdown>
								<Nav.Link href="/">Contact</Nav.Link>
							</Nav>
						</Navbar.Collapse>						 */}
					</Navbar>
					<div id="user_money" className="user_money">
						<div id="nav_money"><span>{money}</span><img className="currency_img" src={carrot_img} /></div>
					</div>
					<div id="user_details" className="user_details">
						<svg id="user_icon" xmlns="http://www.w3.org/2000/svg" aria-hidden="true" focusable="false" data-prefix="fas" data-icon="user-circle" className="svg-inline--fa fa-user-circle fa-w-16" role="img" viewBox="0 0 496 512">
							<path id="user_icon_path" fill="yellow" d="M248 8C111 8 0 119 0 256s111 248 248 248 248-111 248-248S385 8 248 8zm0 96c48.6 0 88 39.4 88 88s-39.4 88-88 88-88-39.4-88-88 39.4-88 88-88zm0 344c-58.7 0-111.3-26.6-146.5-68.2 18.8-35.4 55.6-59.8 98.5-59.8 2.4 0 4.8.4 7.1 1.1 13 4.2 26.6 6.9 40.9 6.9 14.3 0 28-2.7 40.9-6.9 2.3-.7 4.7-1.1 7.1-1.1 42.9 0 79.7 24.4 98.5 59.8C359.3 421.4 306.7 448 248 448z"/>
						</svg>
						<div className="user_details_container">
							<div className="user_details_box">								
								<div id="nav_user" className="nav_user" onClick={()=>this.handleClick('casino')}>{username}</div>
								<div id="nav_account" className="nav_user" onClick={()=>this.handleClick('account')}>My account</div>
								<div id="nav_logout" className="nav_user" onClick={()=>this.handleClick('logout')}>Logout</div>
							</div>	
						</div>						
					</div>
					<Row>
						<Col sm={12}>
							<Switch>			
								<Route path={url}>
									{ !this.state.visible ? 
										<Game game={game} user={username} money={money} user_table={user_table} type={type} socket={socket}></Game>									
										: 
										<UserAccount user={username} money={money} user_table={user_table} type={type}></UserAccount> 
									}
								</Route>
							</Switch>							
						</Col>
					</Row>	
				</div>
			</BrowserRouter>
		);
	}
}

export default UserPage;